@mixin spanStyle($name) {
  .#{$name} {
    position: absolute;
    top: 45%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
}
@mixin customFormItem($width: 90px, $lineHeight: 32px, $labelColor: #000, $contentColor: #000, $hasMargin: true) {
  .custom-form-item{
    overflow: hidden;
    margin: 0 8px 8px 0;
    @if $hasMargin == false { margin: 0 }
    .custom-form-item__label{
      float: left;
      width: $width;
      padding-right: 8px;
      box-sizing: border-box;
      line-height: $lineHeight;
      text-align: right;
      font-size: 14px;
      color: $labelColor;
    }
    .custom-form-item__content{
      position: relative;
      text-align: left;
      margin-left: $width;
      line-height: $lineHeight;
      font-size: 14px;
      white-space: normal;
      word-break: break-word;
      color: $contentColor;
    }
    &:before, &:after{
      content: '';
      display: table;
    }
    &:after{
      clear: both;
    }
  }
}

$types: category, tag;
@each $type in $types {
  .#{$type}-detail{
    width: 100%;
    min-height: 100%;
    background-color: #fff;
    padding: 20px;
    border: 1px solid #e8e8e8;
    @include spanStyle(#{$type}-detail--spin);
    .#{$type}-detail--title{
      position: relative;
      font-size: 22px;
      top: -5px;
      span{
        margin-left: 10px;
        color: #bbb;
      }
    }
    .#{$type}-detail__time{
      margin-right: 15px;
      font-size: 14px;
      color: #8590a6;
    }
    .#{$type}-detail--pagination{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      padding: 10px 20px;
      text-align: right;
    }
  }
}
