@mixin spanStyle($name) {
  .#{$name} {
    position: absolute;
    top: 45%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
}
@mixin customFormItem($width: 90px, $lineHeight: 32px, $labelColor: #000, $contentColor: #000, $hasMargin: true) {
  .custom-form-item{
    overflow: hidden;
    margin: 0 8px 8px 0;
    @if $hasMargin == false { margin: 0 }
    .custom-form-item__label{
      float: left;
      width: $width;
      padding-right: 8px;
      box-sizing: border-box;
      line-height: $lineHeight;
      text-align: right;
      font-size: 14px;
      color: $labelColor;
    }
    .custom-form-item__content{
      position: relative;
      text-align: left;
      margin-left: $width;
      line-height: $lineHeight;
      font-size: 14px;
      white-space: normal;
      word-break: break-word;
      color: $contentColor;
    }
    &:before, &:after{
      content: '';
      display: table;
    }
    &:after{
      clear: both;
    }
  }
}

.categories{
  width: 100%;
  min-height: 100%;
  background-color: #fff;
  border: 1px solid #e8e8e8;
  padding: 50px 80px;
  text-align: center;
  .categories--title {
    font-size: 30px;
    font-weight: 700;
  }
  .categories--total{
    font-size: 14px;
    margin-bottom: 20px;
  }
}
