@mixin spanStyle($name) {
  .#{$name} {
    position: absolute;
    top: 45%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
}
@mixin customFormItem($width: 90px, $lineHeight: 32px, $labelColor: #000, $contentColor: #000, $hasMargin: true) {
  .custom-form-item{
    overflow: hidden;
    margin: 0 8px 8px 0;
    @if $hasMargin == false { margin: 0 }
    .custom-form-item__label{
      float: left;
      width: $width;
      padding-right: 8px;
      box-sizing: border-box;
      line-height: $lineHeight;
      text-align: right;
      font-size: 14px;
      color: $labelColor;
    }
    .custom-form-item__content{
      position: relative;
      text-align: left;
      margin-left: $width;
      line-height: $lineHeight;
      font-size: 14px;
      white-space: normal;
      word-break: break-word;
      color: $contentColor;
    }
    &:before, &:after{
      content: '';
      display: table;
    }
    &:after{
      clear: both;
    }
  }
}

.article-content{
  width: 100%;
  padding-bottom: 50px;
  @include spanStyle(article-content-spin);
  .article-content__wrapper{
    height: 100%;
    padding-right: 3px;
    .article-content-list{
      position: relative;
      border: 1px solid #ebedf0;
      padding: 16px 24px;
      margin-bottom: 10px;
      transition: all .2s linear;
      background-color: #fff;
      &:hover {
        box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
        transform: translate3d(0, -5px, 0);
        cursor: pointer;
      }
      .title {
        color: #394d69;
        font-size: 1.4rem;
        font-weight: 600;
        line-height: 1.2;
        cursor: pointer;
        &:hover {
          color: #48b2ff;
        }
      }
      .description {
        // padding: 15px 10px;
        cursor: pointer;
        max-height: 260px;
        overflow: hidden;
      }
      .create-time {
        font-size: 0.5em;
        padding-left: 20px;
      }
      .list-item-action {
        line-height: 26px;
        vertical-align: middle;
        margin-top: 10px;
        color: rgba(0, 0, 0, 0.45);
        a {
          color: inherit;
        }
      }
    }
  }
  .article-content__pagination{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    padding: 10px 15px;
    text-align: right;
    background-color: #f3f6f7;
  }
}
@media screen and (max-width: 736px){
  .ant-divider-inner-text {
    max-width: calc(100vw - 77px);
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

