@mixin spanStyle($name) {
  .#{$name} {
    position: absolute;
    top: 45%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }
}
@mixin customFormItem($width: 90px, $lineHeight: 32px, $labelColor: #000, $contentColor: #000, $hasMargin: true) {
  .custom-form-item{
    overflow: hidden;
    margin: 0 8px 8px 0;
    @if $hasMargin == false { margin: 0 }
    .custom-form-item__label{
      float: left;
      width: $width;
      padding-right: 8px;
      box-sizing: border-box;
      line-height: $lineHeight;
      text-align: right;
      font-size: 14px;
      color: $labelColor;
    }
    .custom-form-item__content{
      position: relative;
      text-align: left;
      margin-left: $width;
      line-height: $lineHeight;
      font-size: 14px;
      white-space: normal;
      word-break: break-word;
      color: $contentColor;
    }
    &:before, &:after{
      content: '';
      display: table;
    }
    &:after{
      clear: both;
    }
  }
}

.statistics{
  .statistics--card{
    padding: 20px 24px 8px;
    .statistics--card--top{
      .statistics--card--top--title{
        height: 22px;
        color: rgba(0,0,0,.45);
        font-size: 14px;
        line-height: 22px;
      }
      .statistics--card--top--total{
        height: 38px;
        margin: 4px 0 0 0;
        overflow: hidden;
        color: rgba(0,0,0,.85);
        font-size: 30px;
        line-height: 38px;
        white-space: nowrap;
        text-overflow: ellipsis;
        word-break: break-all;
      }
    }
    .statistics--card--content{
      position: relative;
      margin: 5px 0 12px 0;
      height: 40px;
      color: rgba(0,0,0,.65);
      .statistics--card--content--fixed{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        span{
          display: inline-block;
          font-size: 14px;
          line-height: 22px;
          &:first-child{
            margin-right: 20px;
          }
          i{
            margin-left: 5px;
            font-style: normal;
          }
          .anticon-caret-up{
            top: -1px;
            color: #f5222d;
            margin-left: 4px;
          }
          .anticon-caret-down{
            top: -1px;
            color: #52c41a;
            margin-left: 4px;
          }
        }
      }
    }
    .statistics--card--footer{
      padding-top: 9px;
      border-top: 1px solid #e8e8e8;
      color: rgba(0,0,0,.85);
      line-height: 22px;
      span{
        color: rgba(0,0,0,.65);
        margin-right: 10px;
      }
    }
    .ant-card-body{
      padding: 0;
    }
  }
}
